import React, { useEffect } from 'react';
import { categories } from './constants';
import CategoryCard from '../../components/CategoryCard/CategoryCard';
import Slider from '../../components/Slider/Slider';
import BannerZamdikhanov from '../../components/BannerZamdikhanov/BannerZamdikhanov';
import css from './MainPage.module.scss';

function MainPage() {
    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);
    return (
        <main>
            <Slider />
            <div className={css.wrap}>
                <div className={css.category_container}>
                    <h2 className={css.title}></h2>
                    <ul className={css.cards}>
                        {categories.map((item) => {
                            return (
                                <li className={css.cards__card} key={item.title}>
                                    <CategoryCard
                                        title={item.title}
                                        imgUrl={item.imgUrl}
                                        linkUrl={item.linkUrl}
                                    />
                                </li>
                            );
                        })}
                    </ul>
                </div>
                <div className={css.news_container}>
                    <h2 className={css.title}> </h2>
                    <p className={css.news__text}>
                               . 
                            .    
                          ,    JS.    
                             ,    
                         React.        
                          .
                    </p>
                    <p className={css.news__text}>
                        :     WEB .  
                          React, Typescript,   canvas (  
                         ,      ).
                    </p>
                </div>
                <div className={css.news_container}>
                    <BannerZamdikhanov />
                </div>
                <div className={css.news_container}>
                    <h2 className={css.title}></h2>
                    <p className={css.news__text}>
                         :        . 
                            .   
                        .
                    </p>
                    <p className={css.news__text}>
                        :       404.
                    </p>
                    <p className={css.news__text}>
                        :  .      
                        mockapi.io.    .
                    </p>
                    <p className={css.news__text}>
                        :      .
                    </p>
                    <p className={css.news__text}>
                        :       
                        .
                    </p>
                    <p className={css.news__text}>
                        :   ""    
                         .
                    </p>
                    <p className={css.news__text}>
                        :   ,    canvas.
                    </p>
                </div>
            </div>
        </main>
    );
}

export default MainPage;
